button{
    cursor:pointer
}
button.rec-dot {
  background-color: rgb(17, 14, 14);
  box-shadow: 0 0 1px 3px rgba(163, 160, 160, 0.5);
}

button.rec-dot:hover,
button.rec-dot:active,
button.rec-dot:focus {
  box-shadow: 0 0 1px 3px #ffcd1f;
}


.rec.rec-arrow {
  border-radius: 10%;
  background-color: rgb(17, 14, 14);
  margin: 30px;
}

button.rec.rec-arrow:hover {
  border-radius: 50%;
  color: black;
  background-color: rgba(163, 160, 160, 0.5);
  box-shadow: 0 0 1px 3px rgba(163, 160, 160, 0.5);
  cursor:pointer
}

.rec-carousel-item:focus {
  box-shadow: inset 0 0 1px 1px lightgrey;
}

 body{
  overflow-x: hidden;
}

.content{
  position: relative;
  min-height: 100vh;
}

.footer{
  position: relative;
  bottom: 0;
  width: 100%;
  height: 35px;
  background-color: black;
} 